import React from "react";

function Login(props) {
  const {
    email,
    password,
    setPassword,
    setEmail,
    handleLogin,
    handleSignUp,
    passwordError,
    emailError,
    hasAccount,
    setHasAccount,
  } = props;
  return (
    <div>
      <input
        type="text"
        autoFocus
        require
        value={email}
        onChange={(e) => setEmail(e.target.value)}
      ></input>
      <p>{emailError}</p>
      <input
        type="password"
        require
        value={password}
        onChange={(e) => setPassword(e.target.value)}
      ></input>
      <p>{passwordError}</p>
      <div>
        {hasAccount ? (
          <>
            <button onClick={handleLogin}>SignIn</button>
            <p>Don't have account<span onClick={()=> setHasAccount(!hasAccount)}>signUp</span></p>
          </>
        ) : (
          <>
            <button onClick={handleSignUp}>SignUp</button>
            <p>Already have a account ?<span onClick={()=> setHasAccount(!hasAccount)}>SinIn</span></p>
          </>
        )}
      </div>
    </div>
  );
}

export default Login;
