import logo from "./logo.svg";
import Navbar from "./components/navbar";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./App.css";
import Home from "./components/pages/Home";
import NoMatch from "./components/pages/404";
import About from "./components/pages/About";
import "./materialize.css";
import WR from "./components/pages/WR";
import GamesPage from "./components/pages/GamesPage";
import Feedback from "./components/pages/Feedback";
import Softwares from "./components/pages/Softwares";
import NytuoLauncher from "./components/pages/NytuoLauncher";
import TermsAndConditions from "./components/pages/TermsAndConditions";
import VITF from "./components/pages/VITF";
import Privacy from "./components/pages/Privacy";
import Help from "./components/pages/Help";
import TTD from "./components/pages/TTD";
import AE from "./components/pages/AE";
import FWD from "./components/pages/FWD";
import LA from "./components/pages/LA";
import LAATIM from "./components/pages/LAATIM";
import SF from "./components/pages/SF";
import SFO from "./components/pages/SFO";
import SGB from "./components/pages/SGB";
import SNRE from "./components/pages/SN";
import TB from "./components/pages/TB";
import React, { useState, useEffect, Link } from "react";
/* import fire from "./fire";
 */import Login from "./components/Login";
import firebase from "firebase/app";
import "firebase/analytics";

// Add the Firebase products that you want to use
import "firebase/auth";
import "firebase/firestore";

var firebaseConfig = {
  apiKey: "AIzaSyDPzOq-FrGR0sGgCYr23ThqueukBE1YMiw",
  authDomain: "nytuowebsite.firebaseapp.com",
  databaseURL: "https://nytuowebsite-default-rtdb.firebaseio.com",
  projectId: "nytuowebsite",
  storageBucket: "nytuowebsite.appspot.com",
  messagingSenderId: "654311568763",
  appId: "1:654311568763:web:8450fa1db6944a8ef639de",
  measurementId: "G-9NLVVTTMP4"
};
firebase.initializeApp(firebaseConfig);
firebase.analytics();

function App() {
  const [User, setUser] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [hasAccount, setHasAccount] = useState(false);
  const clearInputs = () => {
    setEmail("");
    setPassword("");
  };
  const clearErrors = () => {
    setEmailError("");
    setPasswordError("");
  };

  const handleLogin = () => {
    clearErrors();
    firebase
      .auth()
      .signInWithEmailAndPassword(email, password)
      .catch((err) => {
        switch (err.code) {
          case "auth/invalid-email":
          case "auth/user-disabled":
          case "auth/user-not-found":
            setEmailError(err.message);
            break;
          case "auth/wrong-password":
            setPasswordError(err.message);
            break;
        }
      });
  };

  const handleSignUp = () => {
    clearErrors();

    firebase
      .auth()
      .createUserWithEmailAndPassword(email, password)
      .catch((err) => {
        switch (err.code) {
          case "auth/email-already-in-use":
          case "auth/invalid-email":
            setEmailError(err.message);
            break;
          case "auth/weak-password":
            setPasswordError(err.message);
            break;
        }
      });
  };

  const handleLogOut = () => {
    firebase.auth().signOut();
  };

  const authListener = () => {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        clearInputs();
        setUser(user);
      } else {
        setUser("");
      }
    });
  };

  var Gprovider = new firebase.auth.GoogleAuthProvider();
  const GoogleAuth = () => {
    firebase
      .auth()
      .signInWithPopup(Gprovider)
      .then((result) => {
        /** @type {firebase.auth.OAuthCredential} */
        var credential = result.credential;

        // This gives you a Google Access Token. You can use it to access the Google API.
        var token = credential.accessToken;
        // The signed-in user info.
        var user = result.user;
        // ...
      })
      .catch((error) => {
        // Handle Errors here.
        var errorCode = error.code;
        var errorMessage = error.message;
        // The email of the user's account used.
        var email = error.email;
        // The firebase.auth.AuthCredential type that was used.
        var credential = error.credential;
        // ...
      });
  };
 
  var MProvider = new firebase.auth.OAuthProvider('microsoft.com');
  const MicrosoftAuthent = () => {
    firebase.auth().signInWithPopup(MProvider)
  .then((result) => {
    // IdP data available in result.additionalUserInfo.profile.
    // ...

    /** @type {firebase.auth.OAuthCredential} */
    var credential = result.credential;

    // OAuth access and id tokens can also be retrieved:
    var accessToken = credential.accessToken;
    var idToken = credential.idToken;
  })
  .catch((error) => {
    // Handle error.
  });
  }
  useEffect(() => {
    authListener();
  }, []);
const MicrosoftAuthentLink = () => {
  firebase.auth().currentUser.linkWithPopup(MProvider)
    .then((result) => {
      // Microsoft credential is linked to the current user.
      // IdP data available in result.additionalUserInfo.profile.
      // OAuth access token can also be retrieved:
      // result.credential.accessToken
      // OAuth ID token can also be retrieved:
      // result.credential.idToken
    })
    .catch((error) => {
      // Handle error.
    });
}
  return (
    <>
      <Router>
        <Navbar />
        <Switch>
          <Route path="/" exact component={Home} />

          <Route path="/about" component={About} />
          <Route path="/winrun" component={WR} />
          <Route path="/vincentInTheForest" component={VITF} />
          <Route path="/games" component={GamesPage} />
          <Route path="/feedback" component={Feedback} />
          <Route path="/softwares" component={Softwares} />
          <Route path="/nytuo-launcher" component={NytuoLauncher} />
          <Route path="/TAC" component={TermsAndConditions} />
          <Route path="/privacy" component={Privacy} />
          <Route path="/help" component={Help} />
          <Route path="/theTardisDefender" component={TTD} />
          <Route path="/asteroidEscape" component={AE} />
          <Route path="/fireWallDefender" component={FWD} />
          <Route path="/lutinAdventure" component={LA} />
          <Route path="/legendAdventureAndTheInfernalMaze" component={LAATIM} />
          <Route path="/shootFighter" component={SF} />
          <Route path="/shootFighterOrigins" component={SFO} />
          <Route path="/superGeoffreyBros" component={SGB} />
          <Route path="/sansNomReedition" component={SNRE} />
          <Route path="/tanksBattle" component={TB} />
          <Route path="*" component={NoMatch} />
        </Switch>
      </Router>
      {User ? (
        <>
          <p>You're loginIn</p>
          <button onClick={handleLogOut}>LogOut</button>
        </>
      ) : (
        <>
          <Login
            email={email}
            setEmail={setEmail}
            password={password}
            setPassword={setPassword}
            handleLogin={handleLogin}
            handleSignUp={handleSignUp}
            hasAccount={hasAccount}
            setHasAccount={setHasAccount}
            emailError={emailError}
            passwordError={passwordError}
          />
          <button onClick={GoogleAuth}>Login in with Google</button>
          <button onClick={MicrosoftAuthent}>Login in with Microsoft</button>

        </>
      )}
    </>
  );
}

export default App;
